/*商品列表*/
.goodsList{

  ul {

    li{
      border-bottom: 1px solid #ddd;

      a{
        display: block;
        padding: 0.28rem 0;

        border-top: 1px solid #ddd;
        display: flex;
        justify-content: space-between;
      }
      /*商家图片*/
      .logo{
        width: 1.3rem;
        height: 1.3rem;
        img{
          width: 100%;
          height: 100%;
        }
      }
      /*商家详情*/
      .goodsInfo{
        width: calc( 100% - 1.45rem );
        min-height: 3.0rem;
        overflow: hidden;
        &>div{
          margin-bottom: .15rem;
        }

        .title{
          font-weight: bold;
          color: #333;
        }

        /*评分*/
        .star{
          height: .43rem;
          display: flex;
          font-size: 0.14rem;
          color: #666;
          display: flex !important;
          justify-content: space-between;
          align-items: center;
          overflow: hidden;
          .delivery{
            color: #fff;
            border-radius: 0.05rem;
            font-size: 0.2rem;
            background: #009bff;
            span{
              margin: 0 .1rem;
            }
          }
          .ant-rate{
            width: 1.20rem !important;
            height: .43rem !important;
            display: inherit !important;
            justify-content: inherit;
            align-items: inherit;
            overflow: hidden;
            padding-bottom: 0.43rem;
            li{
              flex: 1 !important;
              font-size: 0.12rem;
              height: 100% !important;
              line-height: 0.43rem;
              margin: 0 !important;

            }
          }
          span{
            margin: 0 0.1rem;
          }
        }

        .moneylimit{
          display: flex;
          justify-content: space-between;
          font-size: 0.12rem;
          span{
            padding: 0 0.1rem;
          }
          .left{
            color: #666;
            .startPrice{
              position: relative;
              &::after{
                position: absolute;
                right: 0;
                top:0;
                bottom: 0;
                margin: auto;
                content: '';
                width: 1px;
                height: 0.2rem;
                background: #ddd;

              }
            }
          }
          .right{
            color: #999;
          }
        }

        .type{
          font-size: 0.14rem;
        }

        .discount{
          height: .9rem;
          overflow: hidden;
          padding-top: 0.15rem;
          font-size: 0.12rem !important;
          border-top: 1px dashed #ddd;
          span:first-child{
            width: 0.29rem;
            height: 0.29rem;
            line-height: 0.29rem;
            text-align: center;
            border-radius: .03rem;
            background: rgb(112, 188, 70);
            margin: 0 0.1rem;
            color: #fff;
          }
          .first{

            span:first-child{
              width: 0.29rem;
              height: 0.29rem;
              line-height: 0.29rem;
              text-align: center;
              border-radius: .03rem;
              background: rgb(112, 188, 70);
              margin: 0 0.1rem;
              color: #fff;
            }
          }
        }
      }
    }
  }
}