.back{
  position: absolute;
  left: .22rem;
  top: .13rem;
  color: #fff;
  display: block;
  font-size: 0.4rem;
}

.shopList{
  position: relative;

  .shopContent{

    .hd{
      height: 2rem;
      background: url("./img/topBg.webp") top/cover;
      position: relative;
      .shopIcon{
        position: absolute;
        left: 0;
        right: 0;
        bottom: -.3rem;
        margin: auto;
        width: 1.5rem;
        height: 1.5rem;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }

    .shopTitle{
      margin-top: .66rem;
      text-align: center;
      & > div{

      }
      .title{
        font-size: .4rem;
        font-weight: bold;
        margin-bottom: 0.18rem;
      }
      .comment{
        font-size: 0.2rem;
        color: #666;
        margin-bottom: 0.18rem;
        span{
          margin: 0 .10rem;
        }
      }
      .cash{
        width: 2.88rem;
        height: 0.48rem;
        margin:0 auto 0.18rem;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .newuser{
        width: 5.3rem;
        font-size: .22rem;
        margin:0 auto 0.18rem;
        text-align: center;
        display: flex;
        justify-content: space-between;
        align-items: center;
        & > span{
          width: .64rem;
          height: .46rem;
          line-height: .46rem;
        }
        .first{
          transform: scale(0.8);
          background: rgb(112, 188, 70);
          border-radius: 3px;
          color: #fff;
        }
        .userReduce{
          width: calc( 100% - .8rem*2 );
          line-height: 0.46rem;
          background: skyblue;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .discount{
          width: 1.2rem;
          color: #999;
        }
      }
      .tip{
        width: 5.9rem;
        height: .46rem;
        line-height: .46rem;
        transform:scale(0.9);
        color: #999;
        margin:0 auto 0.18rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .foodlist{

      ul{

        li{
          padding: 0 0.3rem;
          padding-bottom: .35rem;
          display: flex;
          justify-content: space-between;
          border-bottom: 1px solid #ddd;
          .foodIcon{
            width: 1.9rem;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .foodInfo{
            width: calc( 100% - 2.2rem );

            .info{
              color: #999;

              &>p{
                margin: 0;
              }
              .title{
                font-size: .4rem;
                font-weight: bold;
                color: #333;
              }
              .goodComment {
                span{

                margin-right: 0.16rem;
                }
              }
            }

            .price{
              display: flex;
              line-height: .55rem;
              justify-content: space-between;

              .money{
                color: red;
              }

              .addOrReduce{
                width: 1.5rem;
                color: blue;
                display: flex;
                justify-content: space-between;

                .num{
                  color: #333;
                  font-weight: bold;
                  font-size: 12px !important;
                }
                span{
                  font-size: 0.45rem !important;
                }
              }
            }
          }
        }
      }

    }
  }
}

.buyCart{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: .96rem;
  background: #505052;
  display: flex;
  justify-content: space-between;
  .cart{
    position: relative;
    width: 5.4rem;

    .cartIcon{
      position: absolute;
      top: -.2rem;
      left: .25rem;
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      text-align: center;
      font-size: 0.8rem;
      color: #fff;
      background: deepskyblue ;
      .num{
        position: absolute;
        top: 0;
        right: 0;
        width: .4rem;
        height: .4rem;
        line-height: 0.4rem;
        background: red;
        color:#fff;
        text-align: center;
        border-radius: 50%;
        font-size: 12px;
      }
    }
    .countPrice{
      margin-left: 1.54rem;
      color: #fff;
      font-size: .40rem;
    }

  }
  .buy{
    width: 2.1rem;
    text-align: center;
    line-height: .96rem;
    background: #38ca73;
    color: #fff;
    font-weight: bold;
    font-size: .4rem;
  }
}