.my{
  background: #f5f5f5;
  height: 100vh;
}
.avatar{
  height: 2.20rem;
  background: #00a8ff;
  display: flex;
  justify-content: space-between;
  .icon{
    width: 1.80rem;
    display: inherit;
    justify-content: center;
    align-items: center;
    img {
      width: 1.2rem;
      height: 1.2rem;
      border-radius: 50%;
    }
  }

  .userInfo{
    width: 4.4rem;
    display: inherit;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    .con{
      font-size: .4rem;
      font-weight: bold;
      margin-bottom: .18rem;
      a{
        color: #fff;
      }
      .login{

      }
    }
    .des{
      font-size: 0.24rem;
    }

  }

  .arrow{
    width: .8rem;
    text-align: center;
    line-height: 2.2rem;
    color: #fff;
  }
}

.myMoney{
  height: 1.66rem;
  display: flex;
  background: #fff;
  margin-bottom: .2rem;
  a{
    flex: 1;
    border-right: 1px solid #ddd;
    display: inherit;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span{
      text-align: center;
      font-size: 0.6rem;
      color: #ff9900;
    }
    p{
      color: #666;
    }
  }
  a:last-child{
    border: none;
  }
}

.mainInfo{

  .title{
    color: #333;
    font-size: .35rem;
    margin-left: 0.3rem;
  }

  .infoList{
    height: .9rem;
    line-height: .9rem;
    margin-bottom: .2rem;
    display: block;
    border-style: solid;
    border-color: #ddd;
    border-width: 1px 0;
    background: #fff;
    .icon{
      margin-left: 0.3rem;
      font-size: 0.4rem;
    }

  }

}


























